body {
  width: 100%;
  height: 100vh;
  background-color: #691E06;
}

#root {
  width: 100%;
  height: 100vh;
}

main {
  width: 100%;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: auto;
}

.container {
  width: 350px;
  height: 480px;
  background-color: white;
  margin: auto;
  border-radius: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.formula {
  width: 330px;
  height: 67px;
  margin: 0 20px 0 0;
  text-align: end;
  font-size: 1.2em;
}

.output {
  width: 330px;
  height: 67px;
  text-align: end;
  font-size: 2.8em;
  margin: 0 20px 0 0;
}

.buttonContainer {
  width: 340px;
  height: 335px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr 1fr;
  -ms-grid-rows: 1fr 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 7px;
  margin: auto;
}

.two-grid-column {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1/3;
}

.two-grid-row {
  grid-row: span 2;
}

.button-style {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  font-size: 1.3em;
  border-width: 0;
  cursor: pointer;
}

.button-style:hover {
  background-color: #d3d3d3;
}

.button-style:active {
  background-color: #c3c3c3;
}

.sign-button-color {
  background-color: #ffedc3;
}

.sign-button-color:hover {
  background-color: #d8c9a7;
}

.sign-button-color:active {
  background-color: #b8ab8c;
}
