$bodybackgroundcolor: #691E06
$calculatorSignButtonColor: #ffedc3
$calculatorSignHoverButtonColor: #d8c9a7
$calculatorSignActiveButtonColor: #b8ab8c
$buttonBackgroundColor: #efefef
$buttonHoverBackgroundColor: #d3d3d3
$buttonActiveBackgroundColor: #c3c3c3

$calculatorMinHeight: 67px

@mixin generalWidthHeight
    width: 100%
    height: 100vh


body
    @include generalWidthHeight
    background-color: $bodybackgroundcolor

#root
    @include generalWidthHeight


main
    @include generalWidthHeight
    display: flex
    flex-direction: column
    justify-content: center
    margin: auto

.container
    width: 350px
    height: 480px
    background-color: white
    margin: auto
    border-radius: 10px
    display: flex
    flex-direction: column
    justify-content: center

.formula
    width: 330px
    height: $calculatorMinHeight
    margin: 0 20px 0 0
    text-align: end
    font-size: 1.2em

.output
    width: 330px
    height: $calculatorMinHeight
    text-align: end
    font-size: 2.8em
    margin: 0 20px 0 0

.buttonContainer
    width: 340px
    height: $calculatorMinHeight * 5
    display: grid
    grid-template-columns: 1fr 1fr 1fr 1fr
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr
    grid-gap: 7px
    margin: auto
    

.two-grid-column
    grid-column: 1/3

.two-grid-row
    grid-row: span 2

.button-style
    width: 100%
    height: 100%
    border-radius: 10px
    font-size: 1.3em
    border-width: 0
    cursor: pointer
    &:hover
        background-color: $buttonHoverBackgroundColor
    &:active
        background-color: $buttonActiveBackgroundColor

.sign-button-color
    background-color: $calculatorSignButtonColor
    &:hover
        background-color: $calculatorSignHoverButtonColor
    &:active
        background-color: $calculatorSignActiveButtonColor
